<template>
	<ion-page>
		<ion-header :translucent="true">
			<ion-toolbar>
				<ion-buttons slot="start">
					<ion-menu-button v-if="false" auto-hide="false" color="theme"></ion-menu-button>
				</ion-buttons>
				<ion-title class="ion-text-center">Онлайн: {{ onlineRef }}</ion-title>
				<ion-buttons slot="end">
					<ion-button color="theme" router-link="/settings">
						<ion-icon class="icon-25" :icon="settingsOutline"></ion-icon>
					</ion-button>
				</ion-buttons>
			</ion-toolbar>
		</ion-header>
		
		<ion-content :fullscreen="true">
			<ion-header collapse="condense">
				<ion-toolbar>
					<ion-title size="large" class="ion-text-center">Онлайн: {{ onlineRef }}</ion-title>
				</ion-toolbar>
			</ion-header>
			<ion-grid>
				<ion-row>
					<ion-col size-md="6" size-xs="12">
						<div class="d-flex ion-justify-content-center">
							<div class="video-container w-100">
								<video
									v-if="isCallRemote"
									id="remoteVideo"
									ref="remoteVideo"
									webkit-playsinline
									playsinline
									autoplay
									class="video-block remote-mini"
								></video>
								<div v-else class="video-block remote-mini"></div>
							</div>
							<video
								muted
								id="localMiniVideo"
								ref="localMiniVideo"
								webkit-playsinline
								playsinline
								autoplay
								class="video-block locale-mini"
							></video>
						</div>
						<div class="d-flex ion-justify-content-center mt-05">
							<ion-button v-if="!isConnectRef" color="theme" size="max" @click="signal.discover(), isConnectRef = !isConnectRef">Подключиться</ion-button>
							<ion-button v-else color="danger" size="max" @click="peerInstance.destroy(), disconnect()">Отключиться</ion-button>
							<ion-button :disabled="!isConnectRef" color="success" size="max" @click="peerInstance.destroy(), signal.discover(), next()">Пропустить</ion-button>
						</div>
					</ion-col>
					<ion-col size-md="6" size-xs="12">
						<div class="d-flex ion-justify-content-center mt-2">
							<video
								muted
								id="localVideo"
								ref="localVideo"
								webkit-playsinline
								playsinline
								autoplay
								class="video-block locale"
							></video>
						</div>
						<div class="d-flex ion-justify-content-center fixed-on-mobile mt-05">
							<ion-item class="chat-bg">
								<ion-icon color="theme" slot="start" class="icon-25" :icon="attachOutline"></ion-icon>
								
								<ion-input v-model="inputMessageRef"></ion-input>

								<ion-icon @click="sendMessage" color="theme" slot="end" class="icon-25" :icon="sendOutline"></ion-icon>
							</ion-item>
						</div>
						<ion-toast
							:color="toastColorRef"
							:is-open="isOpenToast"
							:message="toastMessageRef"
							:duration="3000"
							@didDismiss="isOpenToast = false"
						>
						</ion-toast>
					</ion-col>
				</ion-row>
			</ion-grid>
		</ion-content>
	</ion-page>
</template>

<script lang="ts">
	import { 
		IonToast,
		IonButtons, 
		IonContent, 
		IonHeader, 
		IonMenuButton, 
		IonPage, 
		IonTitle, 
		IonToolbar,
		IonButton,
		IonInput,
		IonItem,
		IonGrid,
		IonIcon,
		IonCol,
		IonRow,
		
	} from '@ionic/vue';
	
	import {
		settingsOutline,
		attachOutline,
		sendOutline,
		
	} from 'ionicons/icons';
	import { defineComponent, ref } from 'vue';
	// import { InAppPurchase2 } from '@ionic-native/in-app-purchase-2';

	// eslint-disable-next-line @typescript-eslint/no-var-requires
	const SocketIoClient = require('socket.io-client');

	// eslint-disable-next-line @typescript-eslint/no-var-requires
	const SimpleSignalClient = require('simple-signal-client');

	export default defineComponent({
		name: 'Folder',
		components: {
			IonButtons,
			IonContent,
			IonHeader,
			IonToast,
			IonMenuButton,
			IonPage,
			IonTitle,
			IonToolbar,
			IonButton,
			IonInput,
			IonItem,
			IonGrid,
			IonIcon,
			IonCol,
			IonRow
		},
		setup() {
			const token = localStorage.getItem('token'),
				isAuth = token !== null;

			const socket = SocketIoClient('localhost:5000', {
				query: {
					token: token
				}
			});	

			const peerInstance = ref<any>(null);

			const signal = ref<any>(null);
			const localVideo = ref<any>(null);
			const remoteVideo = ref<any>(null);
			const localMiniVideo = ref<any>(null);

			const remoteUserId = ref<any>(null);

			const onlineRef = ref(0);
			const isOpenToast = ref(false);
			const isConnectRef = ref(false);

			const isCallRemote = ref(false);

			const toastColorRef = ref<string>('primary');

			const inputMessageRef = ref<string|null>(null);
			const toastMessageRef = ref<string|null>(null);

			const sendMessage = () => {
				if (remoteUserId.value === null) {
					return;
				}  else {
					socket.emit('message', {id: remoteUserId.value, text: inputMessageRef.value});

					inputMessageRef.value = null;
				}
			};

			function getAllFuncs(toCheck: any) {
				let props: any = [];
				let obj = toCheck;

				do {
					props = props.concat(Object.getOwnPropertyNames(obj));
				} while ((obj = Object.getPrototypeOf(obj)));

				return props.sort().filter(function(e: any, i: any, arr: any) {
					if (e!=arr[i+1] && typeof toCheck[e] == 'function') return true;
				});
			}

			const startStreaming = async() => {
				const stream = await navigator.mediaDevices.getUserMedia({
					audio: true,
					video: true,
				});
				const videoTracks = stream.getVideoTracks();
				const signalClient = new SimpleSignalClient(socket, {
					stream: stream
				});

				if (videoTracks.length === 0) {
					isOpenToast.value = true;
					toastMessageRef.value = 'Включите камеру или предоставьте доступ к ней.';
				} else {
					if (localVideo.value && localMiniVideo.value) {
						if ('srcObject' in localVideo.value && 'srcObject' in localMiniVideo.value) {
							localVideo.value.srcObject = stream;
							localMiniVideo.value.srcObject = stream;
						} else if (localVideo.value.src && localMiniVideo.value.src) {
							localVideo.value.src = window.URL.createObjectURL(stream);
							localMiniVideo.value.src = window.URL.createObjectURL(stream);
						}
					}

					const getRandomId = (ids: any) => {
						ids = ids.filter((id: any) => { return id != signalClient.id });

						return ids[Math.floor(Math.random() * ids.length)];
					};

					const onPeer = (peer: any, localStream: any) => {
						peer.addStream(localStream);

						peer.on('stream', (remoteStream: any) => {
							
							if (remoteVideo.value) {
								if ('srcObject' in remoteVideo.value) {
									remoteVideo.value.srcObject = remoteStream;
								} else if (remoteVideo.value.src) {
									remoteVideo.value.src = window.URL.createObjectURL(remoteStream);
								}
							}
						});
					};


					signalClient.on('discover', async (allIDs: any) => {
						onlineRef.value = allIDs.length;
						
						const id = getRandomId(allIDs);
						
						if (id !== undefined) {
							remoteUserId.value = id;
							isConnectRef.value = true;
							isCallRemote.value = true;

							const { peer } = await signalClient.connect(id);

							peerInstance.value = peer;

							console.log(getAllFuncs(peer));

							onPeer(peer, stream);

							peer.on('close', () => {
								console.log('close discover peer');

								isConnectRef.value = false;
								isCallRemote.value = false;
								remoteUserId.value = null;
							});
						} else {
							isOpenToast.value = true;
							toastColorRef.value = 'theme';
							toastMessageRef.value = 'Ожидание пользователей...';

							setTimeout(() => {
								isOpenToast.value = false;
								toastColorRef.value = 'primary';
								toastMessageRef.value = null;
							}, 3000);
						}
					});

					signalClient.on('request', async (request: any) => {
						onlineRef.value = signalClient.peers().length;

						if (!remoteUserId.value) {
							const { peer } = await request.accept();

							peerInstance.value = peer;

							console.log(getAllFuncs(peer));

							peer.addStream(stream);

							isConnectRef.value = true;
							isCallRemote.value = true;
							remoteUserId.value = request.initiator;

							peer.on('stream', (remoteStream: any) => {
								if (remoteVideo.value) {
									if ('srcObject' in remoteVideo.value) {
										remoteVideo.value.srcObject = remoteStream;
									} else if (remoteVideo.value.src) {
										remoteVideo.value.src = window.URL.createObjectURL(remoteStream);
									}
								}

								peer.on('close', () => {
									console.log('close request peer');

									remoteUserId.value = null;
									isCallRemote.value = false;
									isConnectRef.value = false;
								});
							});
						} else {
							await request.reject();

							console.log('Отклонили звонок от(т.к уже заняты): ', request.initiator);
						}
					});

					signalClient.on('reject', async (data: any) => {
						console.log(data)

						isConnectRef.value = false;
						isCallRemote.value = false;
						remoteUserId.value = null;
					});
				}

				return {
					peer: peerInstance,
					stream: stream,
					signal: signalClient,
				};
			};


			if (isAuth) {
				startStreaming().then((result) => {
					signal.value = result.signal;
					peerInstance.value = result.peer.value;


					socket.on('message', (data: any) => {
						console.log('message: ', data);
					});

					socket.on('simple-signal[reject]', (data: any) => {
						console.log('Звонок отклонен, т.к нет доступных пользователей', data);

						// signal.value._closePeer(data.sessionId);

						isCallRemote.value = false;
						remoteUserId.value = null;

						isOpenToast.value = true;
						toastColorRef.value = 'danger';
						toastMessageRef.value = 'Нет доступных пользователей, ожидаем...';

						setTimeout(() => {
							isOpenToast.value = false;
							toastColorRef.value = 'primary';
							toastMessageRef.value = null;
						}, 3000);
					});
				});
			} else {
				//
			}

			const disconnect = () => {
				remoteUserId.value = null;
				isCallRemote.value = false;
				isConnectRef.value = false;
			};

			const next = () => {
				remoteUserId.value = null;
				isCallRemote.value = false;
				isConnectRef.value = false;
			};

			return {
				toastMessageRef,
				inputMessageRef,
				settingsOutline,
				localMiniVideo,
				startStreaming,
				toastColorRef,
				attachOutline,
				isConnectRef,
				isCallRemote,
				peerInstance,
				sendMessage,
				remoteVideo,
				sendOutline,
				isOpenToast,
				localVideo,
				disconnect,
				onlineRef,
				signal,
				next,

			};
		}
	});
</script>

